// these can all live on the cell
export var removeElementFromArray = function (element, array) {
    array.splice(array.indexOf(element), 1);
};
export var isNeighbor = function (current, compare) {
    if (current.index === compare.index)
        return false;
    return (current.row === compare.row ||
        current.column === compare.column ||
        current.block === compare.block);
};
export var isConflict = function (cell, newValue) {
    return newValue !== 0 && newValue === cell.currentValue;
};
export var wasConflict = function (cellOne, cellTwo) {
    return cellOne.hasConflictsWith.includes(cellTwo.index);
};
export var aggregateCurrentValues = function (cells) {
    var initial = Array.from({ length: 9 }, function (x, i) { return i + 1; }).reduce(function (acc, num) {
        acc["r" + num] = [];
        acc["c" + num] = [];
        acc["b" + num] = [];
        return acc;
    }, {});
    return cells.reduce(function (acc, cell) {
        if (cell.currentValue === 0)
            return acc;
        acc["r" + cell.row].push(cell.currentValue);
        acc["c" + cell.column].push(cell.currentValue);
        acc["b" + cell.block].push(cell.currentValue);
        return acc;
    }, initial);
};
